            >
              <table
                class="text-center w-100"
                style="min-width:680px;"
              >
                <tr>
                  <td
                    v-for="(title, index) in [ '規格', '零售價', '可用庫存', '採購中']"
                    :key="title+index"
                    style="width:25%;"
                    class="py-4"
                    v-text="title"
                  />
                  <td style="width:15%">
                    <dropdown
                      :no-dot="true"
                      :title="'7日銷量'"
                      :items="[
                        { text: '7日銷量', type: 'item' },
                        { text: '30日銷量', type: 'item' },
                        { text: '60日銷量', type: 'item' },
                        { text: '90日銷量', type: 'item' },
                      ]"
                    />
                  </td>
                </tr>
              </table>
            </th>
            <th
              style="min-width:200px;"
              class="text-center py-4"
              v-text="'其他資訊'"
            />
          </template>
          <tr
            v-for="({ Id, Name, MarketName, ExchangeRateId, SupplierId, PurchaseUrl, MainId, KeyWord, Remark, Status, ProductSpecData, ProductFileData }, key) in list"
            :key="key"
          >
            <td>
              <label class="customcheckbox m-b-20">
                <input
                  ref="checkbox"
                  type="checkbox"
                  @input="onCheck({ $event, Id, key })"
                >
                <span class="checkmark" />
              </label>
            </td>
            <td>
              <div class="d-flex flex-row comment-row">
                <div 
                  v-for="({url}, i) in ProductFileData"
                  :key="i"
                  class="p-2"
                >
                  <img
                    :src="url"
                    style="width:50px;height:50px;"
                  >
                </div>
                <div class="comment-text w-100">
                  <li>
                    <a
                      href="javascript:void(0)"
                      class="product-spec"
                      v-text="Name"
                    />
                  </li>
                  <li
                    style="color: #acacac"
                    v-text="`商品主貨號： ${MainId}`"
                  />
                  <li
                    v-for="{Name, Id} in supplierList.filter(({Id})=> Id === SupplierId)"
                    :key="Id"
                    style="color: #acacac"
                    v-text="`供應商： ${Name}`"
                  />
                  <li
                    v-if="Status"
                    style="color: #009688"
                  >
                    正常銷售 <i class="m-r-10 mdi mdi-check" />
                  </li>
                  <li
                    v-else
                    style="color: #acacac"
                  >
                    停用
                  </li>
                  <li>
                    <span
                      v-for="(str, i) in KeyWord.split(',')"
                      :key="i"
                      class="badge badge-pill badge-secondary"
                      v-text="str"
                    />
                  </li>
                </div>
              </div>
            </td>
            <td style="min-width:680px !important;width:auto;padding:0px">
              <table
                class="table-spec"
                style="min-width:680px !important;width:100%;"
              >
                <tbody>
                  <tr
                    v-for="({ Id, ItemAtrNo, SpecOne, SpecTwo, Weight, Stock, PurchasePrice, Price, Sort, Status }, index) in ProductSpecData"
                    :key="Id"
                  >
                    <td style="width:20%">
                      <li v-text="`${SpecOne},${SpecTwo}`" />
                      <li style="color: #acacac"  v-text="`選項貨號：${ItemAtrNo}`" />
                    </td>
                    <td style="width:13%">
                      <li v-text="`${PurchasePrice}`" />
                      <li style="color: #acacac"  v-text="`成本：${Price}`" />
                    </td>
                    <td style="width:13%">
                      <li v-text="`${Stock}`" />
                      <li style="color: #acacac"  v-text="`未配貨：${Stock}`" />
                    </td>
                    <td style="width:22%">
                      <li v-text="`${Stock}`" />
                      <li style="color: #acacac"  v-text="`建議採購：${Stock}`" />
                      <li
                        name="PurchaseQuantity"
                        v-show="false"
                      >
                        採購數量：
                        <input
                          type="text"
                          style="width:60px;"
                        >
                      </li>
                    </td>
                    <td style="width:15%" v-text="Stock" />
                  </tr>
                </tbody>
              </table>
            </td>
            <td>
              <dropdown
                :title="'操作'"
                :items="[
                  { text: '修改', type: 'item', action: 'edit' },
                  { type: 'divider' },
                  { text: Status ? '停止使用' : '啟用', type: 'item', action: 'toggle' },
                  { text: '刪除', type: 'item', action: 'delete' },
                ]"
                @onMenu="onMenu({ ...$event, Id, Status, key, })"
              />
              <hr>
              備註：<br>{{ Remark || '(無備註)' }}
            </td>
          </tr>
        </data-table>
      </div>
    </div>
    <div
      v-if="showModal"
      class="modal-backdrop fade show"
    />
  </div>
</template>

<script>
import dataTable from '@/components/dataTable.vue';
import dropdown from '@/components/dropdown.vue';
import modal from '@/components/modal.vue';
// import addLocation from '@/components/addLocation.vue';
import buttonCont from '@/components/buttonCont.vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Translocation',
  components: {
    dataTable,
    dropdown,
    modal,
    buttonCont,
    // addLocation,
  },
  data() {
    return {
      modifyingId: '',
      // -- modal --
      showModal: false,
      modalTitle: '',
      modalMessage: '',
      modalType: 0, // 1: 只有確認 2: 填單
      // -- modal --
      alertMessage: '',
      classBody: {
        'modal-dialog': true,
        'modal-900': true,
      },
      checkedList: [],
      inputList: {
        basic: [
          { title: '*商品名稱', placeholder: '系統預設的名稱', value: '', type: 'input' },
          { title: '上架名稱', placeholder: '上架時會採用這個名稱', value: '', type: 'input' },
        ],
        weight: [
          { title: '首重公斤數', placeholder: '請輸入', value: '', type: 'input' },
          { title: '首重費', placeholder: '請輸入', value: '', type: 'input' },
        ],
        weightSub: [
          { title: '', placeholder: '*公斤', value: '', type: 'input' },
          { title: '～', placeholder: '*公斤', value: '', type: '' },
          { title: '', placeholder: '*公斤', value: '', type: 'input' },
          { title: '運費', placeholder: '*運費', value: '', type: 'input' },
        ],
        remark: [
          { title: '備註', placeholder: '', value: '', type: 'textarea' },
        ],
      },
      search: {
        status: {
          value: 0,
          list: ['全部', '正常銷售', '停止銷售', '需採購'],
        },
        text: {
          text: '',
          type: 0,
          list: ['廣泛搜尋', '商品名稱', '商品編號', '條碼', '供應商'],
        },
        BeginPrice: '',
        EndPrice: '',
        BeginPurchasePrice: '',
        EndPurchasePrice: '',
        BeginStock: '',
        EndStock: '',
      },
      selectedRate: 0,
      activedTab: 0,
      modalFeeList: [],
      // -- Buttons --
      styleData: [
        {
          forBut: { color: 1, mdi: 1, text: '新增商品' },
          forMod: { title: '新增集運地點', type: '2', message: '1', action: 'onCreate' },
        },
        {
          forBut: { color: 4, mdi: 5, text: '採購模式' },
          forMod: { title: '', type: '', message: 1, action: 'onBuilding' },
        },
        {
          forBut: { color: 2, mdi: 2, text: '正常銷售' },
          forMod: { title: '', type: '', message: 2, action: 'onEnableAll' },
        },
        {
          forBut: { color: 2, mdi: 3, text: '停止銷售' },
          forMod: { title: '', type: '', message: 3, action: 'onStopAll' },
        },
        {
          forBut: { color: 3, mdi: 4, text: '刪除' },
          forMod: { title: '', type: '', message: 4, action: 'onDeleteAll' },
        },
      ],
    };
  },
  computed: {
    ...mapGetters({
      exchangeList: 'ExchangeRate/list',
      supplierList: 'Supplier/list',
      list: 'Product/list',
      pagination: 'Product/pagination',
    }),
    searchPaylaod() {
      const { search, pagination } = this
      const {
        status: { value: SearchStatus },
        text: { type: SearchTextType, text: SearchText },
        BeginPrice, EndPrice, BeginPurchasePrice, EndPurchasePrice, BeginStock, EndStock
      } = search;
      return {
        SearchStatus,
        SearchTextType,
        SearchText,
        BeginPrice,
        EndPrice,
        BeginPurchasePrice,
        EndPurchasePrice,
        BeginStock,
        EndStock,
        pagination,
      };
    },
  },
  created() {},
  mounted() {
    console.log('debug: mounted -> window.pd = this', window.pd = this);
    this.getList({
      ...this.searchPaylaod,
      pagination: {
        Page: 1,
        PageLimit: 10,
      },
    });
    this.getExchangeList({
      Page: 1,
      PageLimit: 50,
    });
    this.getSupplierList({
      Page: 1,
      PageLimit: 50,
    });
  },
  methods: {
    ...mapActions({
      getExchangeList: 'ExchangeRate/read',
      getSupplierList: 'Supplier/read',
      getList: 'Product/read',
      addLocation: 'Product/create',
      changeSetting: 'Product/modify',
      changeStatus: 'Product/update',
      deleteLocation: 'Product/deleted',
    }),
    callModal({ title, type, message, action }) {
      document.body.classList.add('modal-open');
      document.body.style.paddingRight = '17px';
      this.showModal = true;
      this.modalTitle = title || '提示訊息';
      this.modalMessage = message || '';
      this.modalType = type || 0;
      this.action = action || 'closeMod';
      this.alertMessage = '';
    },
    onModalAdd() {
      const { weightSub } = this.inputList;
      const {
        0: { value: beginWeight },
        2: { value: endWeight },
        3: { value: fee },
      } = weightSub;
      const rateId = this.selectedRate.sub;
      const { CurrencyName: name, Rate: rate } = this.exchangeList.filter(({ Id }) => Id === rateId)[0] || {};
      this.modalFeeList.push({ beginWeight, endWeight, fee, name, rate, rateId });
    },
    onConfirm() {
      const action = this[this.action];
      console.log('debug: onConfirm -> action', action);
      if (typeof action === 'function') {
        action();
      }
    },
    closeMod() {
      document.body.classList.remove('modal-open');
      document.body.style.paddingRight = null;
      this.showModal = false;
      this.modalMessage = '';
      this.alertMessage = '';
      Object.keys(this.inputList).forEach((key) => {
        this.inputList[key].forEach((input) => {
          input.value = '';
        });
      });
      this.modalFeeList.splice(0);
    },
    resetSearch() {
      const { search } = this;
      search.status.value = 0;
      search.text.text = '';
      search.BeginPrice = '';
      search.EndPrice = '';
      search.BeginPurchasePrice = '';
      search.EndPurchasePrice = '';
      search.BeginStock = '';
      search.EndStock = '';
    },
    onCreate() {
      
      this.callModal({
        message: '建構中',
        type: 1,
      });

      return;

      const { inputList, selectedRate, modalFeeList } = this;
      const { basic, weight, remark } = inputList;

      const {
        0: { value: Name },
        1: { value: DeliveryLocation },
        2: { value: ContactPerson },
        3: { value: PhoneNumber },
        4: { value: Address },
      } = basic;

      const {
        0: { value: FirstKG },
        1: { value: FirstFee },
      } = weight;

      const [{ value: Remark }] = remark;

      const ExchangeRateId = selectedRate.main;

      const TransFeeData = modalFeeList.map((item) => {
        const { beginWeight, endWeight, fee, rate, rateId } = item;
        return {
          Id: rateId,
          BeginWeight: beginWeight,
          EndWeight: endWeight,
          Price: fee,
          NTPrice: fee * rate,
          ExchangeRateId: rateId,
        };
      });

      if (Name && ExchangeRateId) {
        // if (true) {
        this.addLocation({
          Name,
          DeliveryLocation,
          ContactPerson,
          PhoneNumber,
          Address,
          FirstKG,
          FirstFee,
          ExchangeRateId,
          TransFeeData,
          Status: true,
          Remark,
        })
          .then((result) => {
            this.closeMod();
            this.callModal({
              message: '新增成功',
              type: 1,
            });
            // this.modalMessage = '新增成功';
            this.getList(this.searchPaylaod);
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)及匯率必填';
      }
    },
    onEdit() {
      const { inputList, selectedRate, modalFeeList } = this;
      const { basic, weight, remark } = inputList;

      const {
        0: { value: Name },
        1: { value: DeliveryLocation },
        2: { value: ContactPerson },
        3: { value: PhoneNumber },
        4: { value: Address },
      } = basic;

      const {
        0: { value: FirstKG },
        1: { value: FirstFee },
      } = weight;

      const [{ value: Remark }] = remark;

      const ExchangeRateId = selectedRate.main;

      const TransFeeData = modalFeeList.map((item) => {
        const { beginWeight, endWeight, fee, rate, rateId } = item;
        return {
          // Id: rateId,
          BeginWeight: beginWeight,
          EndWeight: endWeight,
          Price: fee,
          NTPrice: fee * rate,
          ExchangeRateId: rateId,
        };
      });

      if (Name && ExchangeRateId) {
        // if (true) {
        this.changeSetting({
          Id: this.modifyingId,
          data: {
            Name,
            DeliveryLocation,
            ContactPerson,
            PhoneNumber,
            Address,
            FirstKG,
            FirstFee,
            ExchangeRateId,
            TransFeeData,
            Status: true,
            Remark,
          } })
          .then((result) => {
            this.closeMod();
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            // this.modalMessage = '新增成功';
            this.getList(this.searchPaylaod);
          })
          .catch((err) => {
            this.alertMessage = err.message || '不明錯誤';
          });
      } else {
        this.alertMessage = '所有星號(*)必填';
      }
    },
    onMenu({ text, type, index, action, Id, Status, key }) {
      if (action === 'edit') {
        const { Id, Name, DeliveryLocation, ContactPerson, PhoneNumber, Address, FirstKG, FirstFee, ExchangeRateId, Remark, Status, TransFeeData } = this.list[key];

        const { inputList, selectedRate, modalFeeList } = this;

        const { basic, weight, remark } = inputList;

        [Name, DeliveryLocation, ContactPerson, PhoneNumber, Address].forEach((val, index) => {
          basic[index].value = val;
        });

        [FirstKG, FirstFee].forEach((val, index) => {
          weight[index].value = val;
        });

        remark[0].value = Remark;
        selectedRate.main = ExchangeRateId;

        modalFeeList.splice(0);
        TransFeeData.forEach((item) => {
          // console.log(`debug: onMenu -> item`, item)
          const { BeginWeight, EndWeight, Price, NTPrice, ExchangeRateId } = item;
          const rateData = this.exchangeList.filter(({ Id }) => Id === ExchangeRateId)[0] || {};
          const { CurrencyName, Rate } = rateData;
          modalFeeList.push({
            beginWeight: BeginWeight,
            endWeight: EndWeight,
            rate: Rate,
            name: CurrencyName,
            fee: Price,
            rateId: ExchangeRateId,
          });
        });

        this.callModal({
          message: '',
          title: '修改集運地點資料',
          action: 'onEdit',
          type: 2,
        });
        this.modifyingId = Id;
      } else if (action === 'delete') {
        this.deleteLocation(Id)
          .then((result) => {
            this.callModal({
              message: '刪除成功',
              type: 1,
            });
            this.getList(this.searchPaylaod);
          })
          .catch((err) => {
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
          });
      } else if (action === 'toggle') {
        this.changeStatus({
          Id,
          Status: !Status,
        })
          .then((result) => {
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            this.getList({
              ...this.pagination,
            });
          })
          .catch((err) => {
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
          });
      }
    },
    onCheck({ $event, Id, key }) {
      const { checked } = $event.target;
      console.log('debug: onCheck -> checked', checked);
      if (checked && !this.checkedList.includes(Id)) {
        this.checkedList.push(Id);
      }
      if (!checked && this.checkedList.includes(Id)) {
        const index = this.checkedList.indexOf(Id);
        // console.log('debug: onCheck -> index', index);
        if (index > -1) {
          this.checkedList.splice(index, 1);
        }
      }
    },
    onAllCheck(checked) {
      if (checked) {
        const { checkbox } = this.$refs;
        const list = Array.isArray(checkbox)
          ? checkbox
          : [checkbox];
        list.forEach((el) => {
          el.checked = true;
        });
        this.checkedList.splice(0);
        this.list.forEach(({ Id }) => {
          this.checkedList.push(Id);
        });
      } else {
        this.clearAllchecked();
      }
    },
    clearAllchecked() {
      const { checkbox } = this.$refs;
      const list = Array.isArray(checkbox)
        ? checkbox
        : [checkbox];
      list.forEach((el) => {
        el.checked = false;
      });
      this.checkedList.splice(0);
    },
    onBuilding() {
      this.callModal({
        message: '不明錯誤',
        type: 1,
      });
    },
    onEnableAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.changeStatus({
            Id,
            Status: true,
          }));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            // this.closeMod();
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            // this.modalMessage = '修改成功';
            this.getList(this.searchPaylaod);
          })
          .catch((err) => {
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onStopAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.changeStatus({
            Id,
            Status: false,
          }));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            // this.clearAllchecked();
            // this.closeMod();
            this.callModal({
              message: '修改成功',
              type: 1,
            });
            // this.modalMessage = '修改成功';
            this.getList(this.searchPaylaod);
          })
          .catch((err) => {
            // this.clearAllchecked();
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onDeleteAll() {
      if (this.checkedList.length) {
        const promiseList = [];
        this.checkedList.forEach((Id) => {
          promiseList.push(this.deleteLocation(Id));
        });
        Promise.allSettled(promiseList)
          .then((result) => {
            this.clearAllchecked();
            // this.closeMod();
            this.callModal({
              message: '刪除成功',
              type: 1,
            });
            // this.modalMessage = '修改成功';
            this.getList(this.searchPaylaod);
          })
          .catch((err) => {
            this.clearAllchecked();
            this.callModal({
              message: err.message || '不明錯誤',
              type: 1,
            });
            // this.modalMessage = err.message || '不明錯誤';
          });
      }
    },
    onPage(Page) {
      if (+Page !== +this.pagination.Page) {
        this.getList({
          ...this.searchPaylaod,
          pagination: {
            ...this.pagination,
            Page,
          },
        });
        this.clearAllchecked();
      }
    },
  },
};

</script>

<style scoped>
#AddProductModal {
  font-size: 0.85rem;
}
.control-label {
  white-space: nowrap;
}
.font-narmal {
  font-style: normal;
}
.custom-checkbox .custom-control-input:indeterminate~.custom-control-label:before {
  background-color: #dee2e6;
}
.custom-checkbox .custom-control-input:indeterminate~.custom-control-label:after {
  background-image: none;
}
.card-body .form-control {
  width:150px;
}
</style>
